<template>
  <view class="container">
    <uni-nav-bar title="福利中心" color="#fff" backgroundColor="transparent" :border="false" :statusBar="true" :fixed="true">
      <block slot="left">
        <u-icon name="arrow-left" size="28" @click="back"></u-icon>
      </block>
    </uni-nav-bar>
    <view v-for="(item, index) in menuList" :key="index" class="img-item" @click="goToDetail(item)">
      <image :src="item.url" class="list-image" mode="widthFix" />
    </view>
    <!-- 进群 -->
    <u-popup v-model="kefushow" mode="center" border-radius="16">
      <view class="bgbox">
        <view class="content">
          <view class="ma">
            <image class="" :src="wx_group" mode="widthFix" lazy-load="false" binderror="" bindload="" />
          </view>
          <view class="yi">
            微信扫一扫
          </view>
        </view>
        <view class="off" @click="kefushow = false">
          <uni-icons type="close" color="#fff" size="45" />
        </view>
      </view>
    </u-popup>
  </view>
</template>

<script>
export default {
  data () {
    return {
      userInfo: '',
      kefushow: false,
      wx_group: '',
      menuList: [
        {
          type: 1,
          url: 'https://img.alicdn.com/imgextra/i1/2200676927379/O1CN01aogcmq24NdcpsP80I_!!2200676927379.png',
          link: '/pages/index/sign'
        },
        {
          type: 2,
          url: 'https://img.alicdn.com/imgextra/i1/2200676927379/O1CN01Z8deD524NdcnZgi3h_!!2200676927379.png',
        },
        {
          type: 3,
          url: 'https://img.alicdn.com/imgextra/i4/2200676927379/O1CN01zH9bQ224NdcpxApSK_!!2200676927379.png',
          link: '/pages/detail/detail?id=2'
        },
      ]
    }
  },
  onShow () {
    this.$store.dispatch('getUserInfo').then(res => {
      console.log(res)
      this.userInfo = res.data
    })
    this.$store.dispatch('getAppConfig').then((res) => {
      this.wx_group = res.data.wx_group
    })
  },
  methods: {
    goToDetail (item) {
      switch (item.type) {
        case 1:
          this.$common.to({
            url: '/pages/index/sign',
          })
          break
        case 2:
          if (!this.userInfo) {
            uni.showToast({
              title: '登录后再操作',
              icon: 'none',
              mask: true
            })
            return
          }
          let url
          if (this.userInfo.level == 2) {
            url = '/package/mine/daili'
          } else {
            url = '/package/mine/yaoqing'
          }
          this.$common.to({
            tabCur: 1,
            url,
          })
          break
        case 3:
          this.kefushow = true
          break
      }
    },
    back () {
      this.$common.to({
        type: 3,
        url: '/pages/tabBar/home',

      })
    }
  }
}
</script>

<style lang="scss" scoped>
.container {
  padding: 20rpx;
  padding-top: 0;
  background: url('https://img.alicdn.com/imgextra/i1/2200676927379/O1CN01GYJhsN24Ndco7ktYR_!!2200676927379.png') no-repeat;
  background-size: 100% 100%;
  height: 100vh;

  .img-item {
    margin-bottom: 30rpx;
    border-radius: 16rpx;
    overflow: hidden;
    box-shadow: 0 4rpx 16rpx rgba(0, 0, 0, 0.1);

    // 点击效果
    &:active {
      opacity: 0.9;
      transform: scale(0.98);
    }
  }

  .list-image {
    width: 100%;
    display: block;
  }

  ::v-deep .u-mode-center-box {
    background: transparent;
  }

  .bgbox {
    background: url('https://img.alicdn.com/imgextra/i4/2200676927379/O1CN01gTyc2924NdcXV2Wi1_!!2200676927379.png') no-repeat;
    background-size: 100% 100%;
    width: 540rpx;
    height: 680rpx;
    position: relative;

    .content {
      position: absolute;
      left: 50%;
      top: 60%;
      transform: translate(-50%, -50%);

      .ma {
        width: 300rpx;
        height: 300rpx;

        image {
          width: 100%;
          height: 100%;
        }
      }
    }

    .yi {
      text-align: center;
      font-size: 28rpx;
      color: #666;
      line-height: 26px;
      margin-top: 40rpx;
    }

    .off {
      text-align: right;
      padding: 15px;
      box-sizing: border-box;
      position: absolute;
      right: -7%;
      // bottom: -20%;
    }


  }
}
</style>